<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <!-- \ -->
          <el-input placeholder="" v-model="goodsQuery.query" clearable @clear="getGoodDatasList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodDatasList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="addGoods"></el-button>
        </el-col>
      </el-row>
      <el-table :data="goodsList" border stripe style="font-size: 14px">
        <!--   -->
        <el-table-column type="index" label="#">
          <template slot-scope="scope">
            <span>{{(goodsQuery.pagenum - 1) * goodsQuery.pagesize + scope.$index + 1}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column label="()" prop="goods_price" width="105px"></el-table-column>
        <el-table-column label="" prop="goods_weight" width="80"></el-table-column>
        <el-table-column label="" prop="add_time" width="170">
          <!--   -->
          <template slot-scope="scope">{{scope.row.add_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="140">
          <template v-slot="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini"
                       @click="editGoods(scope.row.goods_id)"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="delGoods(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                   :current-page="goodsQuery.pagenum" :page-sizes="[10, 30, 50, 100]"
                   :page-size="goodsQuery.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
    </el-pagination>
    <!-- dialog -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editGoodsForm" :rules="editGoodsFormRules" ref="editGoodsFormRef" label-width="100px">
        <el-form-item label="" prop="goods_name">
          <el-input v-model="editGoodsForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="editGoodsForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="editGoodsForm.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="editGoodsForm.goods_weight"></el-input>
        </el-form-item>
        <!--    --->
        <el-form-item label="">
          <el-form-item :label="item.attr_name+' :'" v-for="item in editGoodsForm.attrs" :key="item.attr_id"
                        label-width="200px">
            <el-checkbox-group v-model="item.attr_vals">
              <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-form-item>
        <!--    -->
        <el-form-item label="" prop="pics">
          <el-upload :action="uploadURL" :headers="headerObj" :on-preview="handlePreview"
                     :on-remove="handleRemove" :file-list="fileList"
                     multiple list-type="picture" :on-success="handleSuccess">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip" style="margin: 10px 0; font-size: 14px">jpg/png500kb</div>
            <!--            <img :src="path" v-for="(path,i) in pics" :key="i">-->
          </el-upload>
        </el-form-item>
        <!--   -->
        <el-form-item label="" prop="goods_introduce">
          <!--  -->
          <quill-editor v-model="editGoodsForm.goods_introduce"></quill-editor>
        </el-form-item>
      </el-form>
      <!-- \  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editConfirmGoods"> </el-button>
      </span>
    </el-dialog>
    <!-- dialog -->
    <el-dialog title="" :visible.sync="editPicDialogVisible" width="50%">
      <img :src="picPreviewPath" class="picStyle">
    </el-dialog>
  </div>
</template>

<script>export default {
  data () {
    return {
      goodsQuery: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      goodsList: [],
      //  
      total: 0,
      // id
      goodsId: '',
      // dialog
      editDialogVisible: false,
      // 
      editGoodsForm: {},
      // 
      editGoodsFormRules: {
        goods_name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_price: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_number: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_weight: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_state: [{
          required: true
        }]
      },
      // 
      editPicDialogVisible: false,
      // 
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // header
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      picPreviewPath: '',
      // 
      temPath: '',
      fileList: []
    }
  },
  created () {
    this.getGoodDatasList()
  },
  methods: {
    // 
    async getGoodDatasList () {
      const { data: res } = await this.$http.get('goods', { params: this.goodsQuery })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data)
      this.goodsList = res.data.goods
      this.total = res.data.total
      // console.log(res.data)
    },
    // 
    handleSizeChange (newSize) {
      this.goodsQuery.pagesize = newSize
      this.getGoodDatasList()
    },
    // 
    handleCurrentChange (newPage) {
      this.goodsQuery.pagenum = newPage
      this.getGoodDatasList()
    },
    //  
    addGoods () {
      this.$router.push('goods/add')
    },
    // 
    async editGoods (id) {
      const { data: res } = await this.$http.get('goods/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //  
      // JSON.parse(JSON.stringify(obj))
      // JSON.stringify jsJSONJSON.parse()js
      res.data.attrs = JSON.parse(JSON.stringify(res.data.attrs))
      res.data.attrs.forEach(item => {
        item.attr_vals = item.attr_vals.split(',')
      })
      // url file-list
      //  push 
      if (this.fileList.length === 0) {
        res.data.pics.forEach(item => {
          const picInfomation = { name: item.pics_id, url: item.pics_mid_url }
          this.fileList.push(picInfomation)
        })
      }
      // console.log(this.fileList.length)
      this.editDialogVisible = true
      this.editGoodsForm = res.data
      // console.log(this.editGoodsForm.goods_id)
      console.log(this.editGoodsForm.pics)
    },
    // 
    editConfirmGoods () {
      // 
      this.$refs.editGoodsFormRef.validate(async (valid) => {
        if (!valid) { return this.$message.error('') }
        // 
        const { data: res } = await this.$http.put('goods/' + this.editGoodsForm.goods_id, this.editGoodsForm)
        // 
        // console.log(res.meta.status)
        if (res.meta.status !== 200) { return this.$message.error('') }
        this.$message.success('')
        // 
        this.getGoodDatasList()
        // dialog
        this.editDialogVisible = false
      })
    },
    // 
    editDialogClosed () {
      this.$refs.editGoodsFormRef.resetFields()
      // 
      this.fileList = []
    },
    // 
    handleSuccess (response) {
      this.$message.success('')
      // 1. 
      const picInfo = { pic: response.data.tmp_path }
      // 2.pushpics editGoodsForm
      this.editGoodsForm.pics.push(picInfo)
      console.log(this.editGoodsForm.pics)
      console.log(this.editGoodsForm)
    },
    // 
    handlePreview (file) {
      // console.log(file.url)
      // 
      this.picPreviewPath = file.url
      // 
      this.editPicDialogVisible = true
    },
    // 
    handleRemove (file, fileList) {
      console.log(file.url)
      console.log('list' + fileList)
      // 1.
      const filePath = file.url
      // 2.pics
      const i = this.editGoodsForm.pics.findIndex(x => x.pics_mid_url === filePath)
      console.log(i)
      // 3.splice
      this.editGoodsForm.pics.splice(i, 1)
    },
    // 
    async delGoods (row) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete(`goods/${row.goods_id}`)
      // 
      // console.log(res.meta.status)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //  
      this.$message.success('')
      // 
      this.getGoodDatasList()
    }
  }
}
</script>

<style scoped>
  .el-checkbox {
    margin: 0 5px !important;
  }

  .picStyle {
    width: 100%;
  }
</style>
