<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- card -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" show-icon :closable="false" center></el-alert>

      <!--  -->
      <el-steps :space="200" align-center :active="activeIndex - 0" finish-status="success" style="margin: 20px 0;">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- tab -->
      <!--form-->
      <el-form :model="addGoodsForm" :rules="addGoodsFormRules" ref="addGoodsFormRef" label-width="100px"
               label-position="top">
        <el-tabs tab-position="left" v-model="activeIndex" :before-leave="befLeave" @tab-click="tabClickChange">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <!--   9 -->
              <el-input v-model="addGoodsForm.goods_price" maxlength="9"
                        onkeyup="this.value=this.value.replace(/[^\d.]/g,'')"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addGoodsForm.goods_weight" ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addGoodsForm.goods_number" maxlength="9"
                        onkeyup="this.value=this.value.replace(/[^\d.]/g,'')"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader v-model="addGoodsForm.goods_cat" :options="cateList"
                           :props="cascaderProps" @change="handleChange" :show-all-levels="false">
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!--  -->
            <el-form-item :label="item.attr_name" v-for="item in manyData" :key="item.attr_id">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb, i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <!--  -->
            <el-form-item :label="item.attr_name" v-for="item in onlyData" :key="item.attr_id">
              <!--  -->
              <el-input v-model="item.attr_vals" placeholder=""></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!--  action -- -->
            <el-upload class="upload-demo" :action="uploadURL" :headers="headerObj"
                       :on-preview="handlePreview" :on-remove="handleRemove"
                       multiple list-type="picture" :on-success="handleSuccess">
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip" style="margin: 10px 0; font-size: 14px">jpg/png500kb
              </div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addGoodsForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="btnAdd" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title=""
               :visible.sync="picDialogVisible" width="50%">
      <img :src="picPreviewPath" class="imageWidth">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'loadsh'

export default {
  data () {
    return {
      // tab
      activeIndex: '0',
      // 
      addGoodsFormRules: {
        goods_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_price: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_weight: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_number: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        goods_cat: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      addGoodsForm: {
        goods_name: '',
        goods_price: '',
        goods_weight: '',
        goods_number: '',
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        attrs: []
      },
      // 
      cateList: [],
      // 
      manyData: [],
      // 
      onlyData: [],
      // 
      cascaderProps: {
        expandTrigger: 'hover',
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      //  token
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      //  
      picPreviewPath: '',
      //  
      picDialogVisible: false
    }
  },
  created () {
    this.getCateData()
  },
  methods: {
    // 
    async getCateData () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // console.log(res.data)
      this.cateList = res.data
    },
    handleChange () {
    },
    //  tab
    befLeave (activeName, oldActiveName) {
      //   tab
      if (oldActiveName === 0 || this.addGoodsForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // tab
    async tabClickChange () {
      switch (this.activeIndex) {
        // 
        case '1': {
          const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
            params: { sel: 'many' }
          })
          if (res.meta.status !== 200) {
            return this.$message.error('')
          }
          // 
          res.data.forEach(item => {
            item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
          })
          console.log(res.data)
          this.manyData = res.data
          break
        }
        // 
        case '2': {
          const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
            params: { sel: 'only' }
          })
          if (res.meta.status !== 200) {
            return this.$message.error('')
          }
          console.log(res.data)
          this.onlyData = res.data
          break
        }
      }
    },
    // 
    handleSuccess (response) {
      this.$message.success('')
      // console.log(response)
      // 1. 
      const picInfo = { pic: response.data.tmp_path }
      // 2.pushpics
      this.addGoodsForm.pics.push(picInfo)
    },
    // 
    handleRemove (file) {
      // 1.
      const filePath = file.response.data.tmp_path
      // 2.pics
      const i = this.addGoodsForm.pics.findIndex(x => x.pic === filePath)
      // 3.splice
      this.addGoodsForm.splice(i, 1)
    },
    // 
    handlePreview (file) {
      //  
      this.picPreviewPath = file.response.data.url
      console.log(file.response.data.url)
      //  
      this.picDialogVisible = true
    },
    // 
    addGoods () {
      this.$refs.addGoodsFormRef.validate(async (valid) => {
        // 
        if (!valid) { return this.$message.error('') }
        // goods_cat attrs
        // this.addForm.goods_cat = this.addForm.goods_cat.join(',')
        // goods_cat
        // : lodash cloneDeep(boj)
        const form = _.cloneDeep(this.addGoodsForm)
        form.goods_cat = form.goods_cat.join(',')
        // 
        this.manyData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            // 
            attr_value: item.attr_vals.join(',')
          }
          this.addGoodsForm.attrs.push(newInfo)
        })
        // 
        this.onlyData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addGoodsForm.attrs.push(newInfo)
        })
        form.attrs = this.addGoodsForm.attrs
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) { return this.$message.error('') }
        this.$message.success('')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId () {
      if (this.addGoodsForm.goods_cat.length === 3) {
        return this.addGoodsForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style scoped>
  .el-checkbox {
    margin: 5px 8px !important;
  }

  .imageWidth {
    width: 100%;
  }
  .btnAdd {
    margin-top: 15px;
  }
</style>
